<template>
  <div style="height:100vh;">
    <div v-if="currentInfo.length > 0"
         class="main">
      <div class="reading-box reading-title">
        <div>房间号</div>
        <div v-if="$route.query.dictValue<=3">起数</div>
        <div v-if="$route.query.dictValue<=3">止数</div>
        <div v-if="$route.query.dictValue<=3">用量</div>
        <div v-else>金额</div>
      </div>
      <div class="contain">
        <div class="reading-box reding-item"
             v-for="item in currentInfo.filter(item=>item.price > 0)"
             :key="item.id">
          <div class="ri-left">{{ item.roomSn }}</div>
          <div class="ri-left"
               v-if="$route.query.dictValue<=3">{{ item.lastDegree }}</div>
          <div class="ri-right">
            <input type="number"
                   v-if="$route.query.dictValue<=3"
                   @blur="degreeNum(item)"
                   placeholder="请输入"
                   v-model="item.degree">
            <input type="number"
                   v-else
                   @blur="degreeNum(item)"
                   readonly
                   placeholder="请输入"
                   v-model="item.price">
          </div>
          <div v-if="$route.query.dictValue<=3">{{item.degree - item.lastDegree >= 0 ? item.degree - item.lastDegree : 0 }}</div>
        </div>
      </div>
      <div class="totle"
           v-if="$route.query.dictValue<=3">
        <span>合计用量:{{ computerAll }}</span>&ensp;
        <span>合计费用:&yen;{{ (computerAll * currentInfo[0].price).toFixed(2) }}</span>
      </div>
      <div class="totle"
           v-else>
        <span>合计费用:&yen;{{ computerAllPrice }}</span>
      </div>
      <mt-button class="reading-save"
                 v-if="$route.query.dictValue<=3 && $route.query.status == 0"
                 @click="save">保存</mt-button>
    </div>
    <div v-else
         style="font-size:0.8rem;margin-top:50px;">暂无费用明细</div>
  </div>
</template>

<script>
import { GetBusinessLifeCostBillByCooperLifeCost } from '@/api/api_business';
import { PostBusinessLifeCostBillBatchDegreeAdd } from '@/api/api_business_hotel_and_scale';
import { Toast } from 'vant';
export default {
    data() {
        return {
            currentInfo: [],
            priceObj: {
                1: 'elecPrice',
                2: 'waterPrice',
                3: 'gasPrice'
            },
            titleObj: {
                1: '电费',
                2: '水费',
                3: '气费',
                4: '清洁费',
                5: '停车费',
                6: '物业费',
                7: '宽带费'
            }
        };
    },
    created() {
        this.$route.query.dictValue = +this.$route.query.dictValue;
        this.$store.commit('setHeader', `${this.titleObj[this.$route.query.dictValue]}费用明细`);
        this.getCurrentInfo(this.$route.query.lifeCostBillId);
    },
    computed: {
        computerAll() {
            let all = 0;
            if (this.currentInfo.length) {
                this.currentInfo.forEach((item) => {
                    if (item.degree - item.lastDegree >= 0) {
                        all += item.degree - item.lastDegree;
                    }
                });
            }
            return all;
        },
        computerAllPrice() {
            let sum = 0;
            if (this.currentInfo.length) {
                for (var i in this.currentInfo) {
                    sum += Number(this.currentInfo[i].price);
                }
            }
            return sum;
        }
    },
    methods: {
        getCurrentInfo(id) {
            let data = {};
            if (this.$route.query.dictValue == 1) {
                data = {
                    checkType: 1,
                    origin: 1
                };
            }
            GetBusinessLifeCostBillByCooperLifeCost(Object.assign({ cooperLifeCostBillId: id }, data)).done((res) => {
                this.currentInfo = res.data.map((item) => {
                    let obj = { id: item.id, lastDegree: '', degree: '', roomSn: item.room.roomSn };
                    let _obj = {
                        1: item.preElecDegree,
                        2: item.preWaterDegree,
                        3: item.preGasDegree
                    };
                    obj.lastDegree = _obj[this.$route.query.dictValue];
                    switch (this.$route.query.dictValue) {
                        case 1:
                            obj.degree = item.elecDegree;
                            obj.price = item.elecPrice;
                            break;
                        case 2:
                            obj.degree = item.waterDegree;
                            obj.price = item.waterPrice;
                            break;
                        case 3:
                            obj.degree = item.gasDegree;
                            obj.price = item.gasPrice;
                            break;
                        case 4:
                            obj.price = item.rubbishPrice;
                            break;
                        case 5:
                            obj.price = item.parkPrice;
                            break;
                        case 6:
                            obj.price = item.propPrice;
                            break;
                        case 7:
                            obj.price = item.broadBandPrice;
                            break;
                        default:
                            break;
                    }
                    return obj;
                });
            });
        },
        degreeNum(item) {
            if (item.lastDegree > item.degree && item.degree != '') {
                Toast({
                    message: `请正确输入${item.roomSn}的止数`,
                    duration: 2000
                });
                item.degree = '';
            }
        },
        save() {
            if (this.$route.query.dictValue <= 3) {
                let num = this.currentInfo.filter((item) => item.degree != '').length;
                if (!num) {
                    Toast({
                        message: '请抄表',
                        duration: 2000
                    });
                    return;
                }
                let data = {
                    checkType: this.$route.query.dictValue,
                    checkData: JSON.stringify(this.currentInfo)
                };
                PostBusinessLifeCostBillBatchDegreeAdd(data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 2000
                    });
                    setTimeout('window.history.go(-1)', 900);
                });
            }
        }
    }
};
</script>
<style scoped>
.main {
    background: #fff;
    margin-top: 0.9rem;
    font-size: 0.8rem;
    margin-bottom: calc(5% + 45px);
    height: 100%;
}
.contain {
    max-height: 60%;
    overflow-y: scroll;
}
.reading-title {
    font-weight: 500;
}
.reading-title div {
    width: 25%;
    text-align: center;
}
.reading-box {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
    border-bottom: 0.01rem solid rgba(0, 0, 0, 0.04);
}
.reding-item {
    color: #888888;
}
.reding-item div {
    width: 25%;
    text-align: center;
}
.ri-right input {
    text-align: center;
    width: 100%;
}
.reading-save {
    background: #ff7d41;
    width: 90%;
    color: #fff;
    margin-top: 2rem;
    font-size: 0.8rem;
    /* position: fixed;
    bottom: 2rem;
    left: 5%; */
}
.totle {
    text-align: left;
    padding: 0.5rem 1.2rem;
    font-weight: 600;
}
</style>



